@import url(https://fonts.googleapis.com/css?family=Oswald:400);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.chat {
    width: 250px;
    height: 400px;
    background-color: antiquewhite;
    float: right;
    position: fixed;
}
.chat_header {
    width: 100%;
    height: 15%;
    background-color: darkseagreen;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
}

.chat_footer {
    width: 100%;
    height: 25%;
    background-color: aquamarine;
    display: flex;
    padding: 10px;
    box-sizing: border-box;
}
.chat_footer textarea {
    resize: none;
}
.chat_body{
    width: 100%;
    height: 60%;
    overflow-y: scroll;
}

.myMessage {
    background-color: darkmagenta;
    width: 200px;
    float: right;
    border-radius: 20px;
    padding: 10px;
    box-sizing: border-box;
    margin: 10px 0;
    word-wrap: break-word;
  }
  .yourMessage {
    background-color: deeppink;
    width: 200px;
    float: left;
    border-radius: 20px;
    padding: 10px;
    box-sizing: border-box;
    margin: 10px 0;
    word-wrap: break-word;
  
  }
.img {
  width: 50px;
  border-radius: 50px;
}


.user{
  position: relative;
  float: right;
  margin-left: auto;
  width: auto;
  background-color:#9b9be5;
}

